<template>
  <div class="app" :class="{'timer-running': isTimerRunning}">
    <transition mode="out-in">
      <div
        v-if="!this.currentPlayer"
        class="section"
        key="section1"
      >
        <Login

          @login="onLogin($event)"
          :players="players"
          class="section"
        />
      </div>
      <div
        v-else
        class="section"
         key="section2"
      >
      <WordGenerator
        class="generator"
        @select="onSelectWord"
        :isTimerRunning="isTimerRunning"
        :currentPlayer="currentPlayer"
        :activePlayerId="activePlayerId"
        :isFinished="isFinished"
      />
      <Timer
        class="timer"
        v-if="isTimerRunning"
        @finish="onFinish"
      />
    </div>
    </transition>
    <div class="animation">
      <Background />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Login from '@/components/Login'
import WordGenerator from '@/components/WordGenerator'
import Timer from '@/components/Timer'
import Background from '@/components/Background'

export default {
  components: {
    Login,
    WordGenerator,
    Timer,
    Background
  },
  data() {
    return {
      teams: [],
      email: '',
      isLoggedIn: false,
      isTimerRunning: false,
      isFinished: false,
      activePlayerId: null
    }
  },
  computed: {
    players() {
      let players = [];
      this.teams.forEach(team => {
        players = [...players, ...team.players]
      });
      return players;
    },
    currentPlayer() {
      return this.players.find(player => player.email === this.email);
    }
  },
  methods: {
    getData() {
      axios.get('/api/teams').then(response => {
        this.teams = response.data;

        this.activePlayerId = this.players.find(player => player.active).id;
      })
    },
    onLogin(email) {
      this.email = email;
    },
    onSelectWord({word, difficulty}) {
      this.isTimerRunning = true;
      let score;
      if (difficulty === 'easy') {
        score = 1;
      }
      if (difficulty === 'medium') {
        score = 2;
      }
      if (difficulty === 'hard') {
        score = 3;
      }
      axios.post('/api/save_word/', {
        word,
        score,
        player_id: this.currentPlayer.id
      });
    },
    onFinish() {
      this.isTimerRunning = false;
      this.isFinished = true;
    },
  },
  created() {
    this.getData();
    setInterval(this.getData, 3000);
  }
}
</script>

